import React from "react";
import "./App.css";
import Sidebar from "./sidebar";
import Table from "./table";


class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = { apiResponse: "" };
        this.state = {
            name: '',
            email: '',
            message: '',
            items:[]
          };
          this.handleChange = this.handleChange.bind(this);
          this.handleSubmit = this.handleSubmit.bind(this);
        }
      
handleChange(key) {
 return function (event) {
              var state = {};
              state[key] = event.target.value;
              this.setState(state);
            }.bind(this);          
    }
  handleSubmit = (event) => {
    event.preventDefault();
    let items = [...this.state.items];
    items.push({
      name: this.state.name,
      email: this.state.email,
      message:this.state.message
    });
    this.setState({
        items,
        name: '',
        email: '',
        message: ''
      });
  };
    callAPI() {
        fetch("http://localhost:9000/testAPI")
            .then(res => res.text())
            .then(res => this.setState({ apiResponse: res }))
            .catch(err => err);
    }

    componentDidMount() {
        this.callAPI();
    }
   

    render() {
        return ( 
         <div class="main-div">
            <div class="base-form">
            <form onSubmit={this.handleSubmit} class="appointment-form">
			    <h3 class="mb-3">Know Your Status</h3>					
                <label> Name:
                <input style={{marginLeft:"10px"}} type="text" value={this.state.name} onChange={this.handleChange('name')} /><br /><br />
                </label>
                <label> Email:
                <input style={{marginLeft:"10px"}} type="email" value={this.state.email} onChange={this.handleChange('email')} /><br /><br />
                </label>
                <label style={{marginLeft:"-15px",position:"absolute"}}>Message:</label>
                <textarea style={{marginLeft:"55px"}} rows="4" cols="50" type="text" value={this.state.message} onChange={this.handleChange('message')} /><br />               
                <input  style={{marginLeft:"55px"}} type="submit" value="Submit" />
	   	  </form>
          </div>
          <div class="sidebar-div">
              <Sidebar/>
          </div>
          <div class="table-div">
          <Table items={this.state.items} />
          </div>
        </div>
        );
    }
}

export default App;
