import React from "react";
import "./sidemenu.css";

class Table extends React.Component {
  render() {
    const items = this.props.items;
 return (
    <div class="main-table">
    <table style={{border:"1px #000000 solid",borderCollapse:"collapse"}}>
        <tr>
         <th style={{border:"1px #000000 solid",textAlign:"left"}}>Name</th>
         <th style={{border:"1px #000000 solid",textAlign:"left"}}>Message</th> 
        </tr>
        {items.map(item => {
              return (
                <tr>
                  <td style={{border:"1px #000000 solid",Color:"Black"}}>{item.name}</td>
                  <td style={{border:"1px #000000 solid",Color:"Black"}}>{item.message}</td>
                </tr>
              );
            })}
    </table>
    </div>
    );
  }
}

export default Table;